export default {
	data() {
		return {
			recommendedCourse: [],
			imgUrl: this.$imgUrl
		}
	},
	onLoad:function(option){
		
	},
	mounted() {
		this.gettingData()
	},
	methods: {
		gettingData () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_courses_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.recommendedCourse = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 路径跳转课程详情
		skipCourseDetails (id) {
			uni.navigateTo({
				url: '../courseDetails/courseDetails?id=' + id
			})
		},
	}
}
